<!--标签管理组件-->
<template>
  <div class="">
    <!--筛选表单-->
    <el-form ref="form" :model="form" inline  size="small">
      <el-form-item>
        <el-input v-model="form.name"  placeholder="标签名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="onSubmit">搜索</el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
    <!--功能按钮-->
    <el-button type="primary" icon="el-icon-edit" size="small" style="margin-bottom: 15px;margin-left: 10px" @click="openDialog">添加标签</el-button>
    <!--数据内容-->
    <el-table
        :data="tableData"
        stripe
        border
        size="small"
        style="width: 100%">
      <el-table-column
          type="index"
          label="序号"
          align="center">
      </el-table-column>
      <el-table-column
          label="标签名称"
          align="center">
        <template slot-scope="scope">
          <el-tag effect="dark" type="warning" v-if="scope.row.id%4===0">
            {{ scope.row.name }}
          </el-tag>
          <el-tag effect="dark" type="danger" v-if="scope.row.id%4===1">
            {{ scope.row.name }}
          </el-tag>
          <el-tag effect="dark" type="success" v-if="scope.row.id%4===2">
            {{ scope.row.name }}
          </el-tag>
          <el-tag effect="dark" type="" v-if="scope.row.id%4===3">
            {{ scope.row.name }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column
          prop="views"
          label="浏览数"
          align="center"
          sortable>
      </el-table-column>
      <!--自定义标签时  prop="title"就会失效  提供scope.row获取行对象   -->
      <el-table-column
          prop="blogQuantity"
          sortable
          label="文章数"
          align="center">
      </el-table-column>
      <el-table-column
          fixed="right"
          label="操作"
          align="center"
          width="180">
        <template slot-scope="scope">
          <el-row>
            <el-col :span="12">
              <el-button type="primary" icon="el-icon-edit" size="small" @click="open(scope.row)">编辑</el-button>
            </el-col>
            <el-col :span="12">
              <el-popconfirm title="确定删除吗？" @confirm="deleteBlog(scope.row)">
                <el-button slot="reference" type="danger" icon="el-icon-delete"  size="small">删除</el-button>
              </el-popconfirm>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page.sync="currentPage"
        :page-sizes="[6, 8, 10, 12]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
    <!--弹出层   -->
    <el-dialog title="添加标签" :visible.sync="dialogFormVisible" :modal-append-to-body="false" width="35%">
      <el-form :model="addForm" :rules="rules" ref="ruleForm">
        <el-form-item label="分类名称" label-width="100px" prop="name">
          <el-input v-model="addForm.name" autocomplete="off" style="width: 250px" @blur="isAvailable(addForm.name)"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addFromConfirm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import request from "../../utils/request";

export default {
  name: "TagsIndex",
  components: {},
  data() {
    return {
      dialogFormVisible: false,//弹出层状态
      fromSubmit:false,//表单是否可以提交
      //表格数据
      tableData: [
        {
          name: '标签标题',
          views:'',//浏览数
          blogQuantity:'200',//文章数
        },
      ],
      currentPage: 1,//当前页码
      pageSize:6,//当前每页显示条数
      total: 0,//总记录数
      //筛选表单数据
      form: {
        name: '',//分类名称
      },
      //弹出层表单
      addForm:{
        name: '',
        id:'',
      },
      rules: {
        name: [
          {required: true, message: '标签名不能为空', trigger: 'blur',},
        ],
      },
    }
  },
//页面加载
  created() {
    this.loadTagPage()
  },
//可以访问DOM元素
  mounted() {
  },
//方法集合
  methods: {
    //弹出框表单失去焦点方法
    isAvailable(val){
      request.get("/tag/getTagByTagName/"+val).then(res=>{
        if(res.data.result){
          this.$message.error("该标签名以存在")
        }else {
          this.$message.success("该标签名可以使用")
          this.fromSubmit=true
        }
      })
    },
    //筛选表单提交功能
    onSubmit() {
      this.loadTagPage()
    },
    //重置按钮
    reset(){
      this.form={}
    },
    //改变当前每一页个数
    handleSizeChange(pageSize) {
      this.pageSize=pageSize
      this.loadTagPage()
    },
    //改变当前页码触发
    handleCurrentChange(pageNum) {
      this.currentPage=pageNum
      this.loadTagPage()
    },
    //获取标签列表
    loadTagPage(){
      request.get("/tag/getTagPage",{params:{
          pageNum:this.currentPage,//向后台传入的页码
          pageSize:this.pageSize,//向后台传入的当前页显示条数
          search:this.form.name,//向后台传入的搜索条件
        }}).then(res=>{
        this.total=res.data.data.tagPage.total
        this.tableData=res.data.data.tagPage.results
      })
    },
    //打开弹窗方法
    openDialog(){
      this.dialogFormVisible=true;
      this.addForm={}
    },
    //删除按钮
    deleteBlog(row){
      //判断当前标签是否有文章
      if(row.blogQuantity!==0){
        request.get("/tag/getBlogTitleByTagId/"+row.id).then(res=>{
          this.$message.warning("当前标签包含文章: "+res.data.data.blogTitles+"。 不能直接删除请先修改文章")
        })
      }else {
        request.delete("/tag/deleteTagById/"+row.id).then(res=>{
          if(res.data.result){
            this.$message.success("删除成功")
          }else {
            this.$message.success("删除失败")
          }
          //重新加载数据
          this.loadTagPage()
        })
      }
    },
    //编辑按钮
    open(row){
      this.dialogFormVisible=true;
      this.addForm.id=row.id
      this.addForm.name=row.name
    },
    //弹窗确定按钮
    addFromConfirm(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.fromSubmit){
            if(this.addForm.id){
              request.put("/tag/updateTagById",this.addForm).then(res=>{
                if(res.data.result){
                  this.$message.success("修改成功")
                }else this.$message.error("修改失败")
                this.loadTagPage();
                this.dialogFormVisible=false;
              })
            }else {
              request.post("/tag/addOneTag",this.addForm).then(res=>{
                if(res.data.result){
                  this.$message.success("添加成功")
                }else this.$message.error("添加失败")
                this.loadTagPage();
                this.dialogFormVisible=false;
              })
            }
            this.fromSubmit=false
          }
        } else {
          return false;
        }
      });
    },
  },
}
</script>

<style scoped>

</style>
        
